<template>
  <div class="jtk-demo demo-groups">
    <div id="workplace"
         class="workplace">
      <chart-node v-for="(item, idx) in chartData"
                  v-bind="item"
                  :key="idx"
                  @edit="editNode(item,idx)"></chart-node>
      <div class="group"
           id="group"
           group="firGroup">
        <div class="ep"></div>
      </div>
    </div>
  </div>
</template>
<script>
// components
import ChartNode from "@/components/ChartNode";

// data
import chartData from '../../json/group-demo.json'
export default {
  name: "Groups",
  data () {
    return {
      chartData,
      jsp: null
    };
  },
  mounted () {
    const _self = this
    this.$nextTick(() => {
      jsPlumb.ready(() => {
        let jsp = jsPlumb.getInstance({ // 
          Endpoint: [
            "Blank",
            {
              cssClass: 'chart-dot',
              hoverClass: 'chart-dot-hover',
              radius: 5
            }
          ],
          Connector: "Straight",
          HoverPaintStyle: {
            'stroke': '#1e8151',
            'strokeWidth': 2
          },
          ConnectionOverlays: [
            [
              "Arrow",
              {
                location: 1,
                visible: true,
                width: 11,
                height: 11,
                id: "Arrow"
              }
            ]
          ],
          Container: "workplace"
        })
        this.jsp = jsp

        // 
        jsp.batch(() => {
          jsPlumb.getSelector(".workplace-chart").forEach(item => {
            _self.initNode(item);
          });
          // jsPlumb.getSelector(".group").forEach(item => {
          //   _self.initNode(item);
          // });

        });
        this.initGroup()

        jsPlumb.fire("jsPlumbDemoLoaded", jsp);
      })
    })
  },
  methods: {
    /**
     * 
     */
    initGroup () {
      let jsp = this.jsp
      jsp.addGroup({
        el: group, // elDOMTypeError: Cannot create property '_isJsPlumbGroup' on string 'group'
        id: 'firGroup', // domgroup 
        draggable: true, // truefalseGroup
        dragOptions: {}, //  katavorio.js dragOptions
        droppable: true, // truefalseGroupGroup
        dropOptions: {
          drop(p) {
            console.log(p)
          }
        }, //  katavorio.js dropOptions
        proxied: true, // 

        revert: true, // true mouseup
        prune: false, // falsetrueGroupgroupjsPlumb
        orphan: false, // falsetrueGroupGroupjsPlumb
        constrain: false, // falsetrueGroup
        ghost: false, // falsetrueGroup'ghost' -  - 
        dropOverride: false, // falsetrueGroupGroup

        collapsed: false, // 
        anchor: "Continuous", // 
        endpoint: ["Dot", { radius: 5 }]
      })
      // jsp.removeGroup("firGroup"); // true
      // jsp.removeFromGroup(someElement); // 
      // jsPlumb.collapseGroup("aGroup");
      // jsPlumb.expandGroup("aGroup");
      // jsp.addToGroup("firGroup", start);

    },
    // node
    initNode (el) {
      // initialise draggable elements.
      //  katavorio.js 
      let _self = this;
      this.jsp.draggable(el, {
        // containment: true,
        start (params) {
          // 
          // console.log(params);
        },
        drag (params) {
          // 
          // console.log(params);
        },
        stop (params) {
          // 
          let id = params.el.id;
          _self.chartData.forEach(item => {
            if (item.id === id) {
              item.nodeStyle.left = params.pos[0] + "px";
              item.nodeStyle.top = params.pos[1] + "px";
            }
          });
        }
      });

      this.jsp.makeSource(el, {
        filter: ".ep",
        // anchor: "Continuous",
        anchor: ["Perimeter", { shape: "Rectangle" }],
        connectorStyle: {
          stroke: "#5c96bc",
          strokeWidth: 2,
          outlineStroke: "transparent",
          outlineWidth: 4
        },
        extract: {
          action: "the-action"
        },
        maxConnections: -1,
        onMaxConnections: function (info, e) {
          alert("Maximum connections (" + info.maxConnections + ") reached");
        }
      });

      this.jsp.makeTarget(el, {
        dropOptions: { hoverClass: "dragHover" },
        anchor: ["Perimeter", { shape: "Rectangle" }],
        allowLoopback: false
      });

      // this is not part of the core demo functionality; it is a means for the Toolkit edition's wrapped
      // version of this demo to find out about new nodes being added.
      //
      this.jsp.fire("jsPlumbDemoNodeAdded", el);
    },
  },

  components: {
    ChartNode
  }
};
</script>

<style scoped>
</style>



